import Component from "jx/core/comps/Component";
export default class GabaritModel extends Component {
    constructor(dataObject) {
        super(dataObject);
        this.gabarit = this.cc({ type: "editionitems.DataModel", data: { children: this.dataObject.gabarit } });
    }
    /**
     * Return component child with path, depends of its id.
     * @example
     * var comp = this.getChildByPath("child1ID.child2ID");
     * @param  {string} path
     * @return {Component}
     */
    getByPath(path) {
        return this.gabarit.getByPath(path);
    }
}
